/**
 * Search Functionality using Fuse.js
 */

import Fuse from 'fuse.js';

class Search {
  constructor() {
    this.searchData = null;
    this.fuse = null;
    this.searchInput = null;
    this.searchResults = null;
    this.searchOverlay = null;
    this.isOpen = false;

    this.init();
  }

  async init() {
    // DOM elements
    this.searchInput = document.getElementById('search-input');
    this.searchResults = document.getElementById('search-results');
    this.searchOverlay = document.getElementById('search-overlay');
    this.searchButton = document.getElementById('search-button');
    this.searchClose = document.getElementById('search-close');

    if (!this.searchInput || !this.searchResults || !this.searchOverlay) {
      return; // Search not enabled
    }

    // Load search data
    await this.loadSearchData();

    // Event listeners
    this.searchButton?.addEventListener('click', () => this.open());
    this.searchClose?.addEventListener('click', () => this.close());
    this.searchInput.addEventListener('input', (e) => this.handleSearch(e.target.value));
    this.searchOverlay.addEventListener('click', (e) => {
      if (e.target === this.searchOverlay) {
        this.close();
      }
    });

    // Keyboard shortcuts
    document.addEventListener('keydown', (e) => {
      // Cmd+K or Ctrl+K to open search
      if ((e.metaKey || e.ctrlKey) && e.key === 'k') {
        e.preventDefault();
        this.open();
      }
      // Escape to close
      if (e.key === 'Escape' && this.isOpen) {
        this.close();
      }
    });
  }

  async loadSearchData() {
    try {
      const response = await fetch('/search.json');
      this.searchData = await response.json();

      // Initialize Fuse.js
      const options = {
        keys: [
          { name: 'title', weight: 0.4 },
          { name: 'excerpt', weight: 0.3 },
          { name: 'content', weight: 0.2 },
          { name: 'tags', weight: 0.05 },
          { name: 'categories', weight: 0.05 }
        ],
        threshold: 0.4,
        distance: 100,
        minMatchCharLength: 2,
        includeScore: true,
        includeMatches: true
      };

      this.fuse = new Fuse(this.searchData, options);
    } catch (error) {
      console.error('Failed to load search data:', error);
    }
  }

  open() {
    this.searchOverlay.classList.add('active');
    this.isOpen = true;
    this.searchInput.focus();
    document.body.style.overflow = 'hidden';
  }

  close() {
    this.searchOverlay.classList.remove('active');
    this.isOpen = false;
    this.searchInput.value = '';
    this.searchResults.innerHTML = '';
    document.body.style.overflow = '';
  }

  handleSearch(query) {
    if (!this.fuse || !query || query.length < 2) {
      this.searchResults.innerHTML = '';
      return;
    }

    const results = this.fuse.search(query);
    this.displayResults(results, query);
  }

  displayResults(results, query) {
    if (results.length === 0) {
      this.searchResults.innerHTML = `
        <div class="search-no-results">
          <p>No results found for "${this.escapeHtml(query)}"</p>
        </div>
      `;
      return;
    }

    const html = results.slice(0, 10).map(result => {
      const item = result.item;
      const excerpt = this.highlightMatches(item.excerpt, result.matches, 'excerpt');
      const title = this.highlightMatches(item.title, result.matches, 'title');

      return `
        <a href="/${item.path}" class="search-result-item">
          <h3 class="search-result-title">${title}</h3>
          <p class="search-result-excerpt">${excerpt}</p>
          <div class="search-result-meta">
            <time>${item.date}</time>
            ${item.categories.length > 0 ? `<span class="separator"></span><span>${item.categories.join(', ')}</span>` : ''}
          </div>
        </a>
      `;
    }).join('');

    this.searchResults.innerHTML = html;
  }

  highlightMatches(text, matches, key) {
    if (!matches) return this.escapeHtml(text);

    const relevantMatches = matches.filter(m => m.key === key);
    if (relevantMatches.length === 0) return this.escapeHtml(text);

    let result = text;
    const indices = [];

    relevantMatches.forEach(match => {
      match.indices.forEach(([start, end]) => {
        indices.push([start, end]);
      });
    });

    // Sort and merge overlapping indices
    indices.sort((a, b) => a[0] - b[0]);
    const merged = [];
    indices.forEach(([start, end]) => {
      if (merged.length === 0 || merged[merged.length - 1][1] < start - 1) {
        merged.push([start, end]);
      } else {
        merged[merged.length - 1][1] = Math.max(merged[merged.length - 1][1], end);
      }
    });

    // Build highlighted string
    let offset = 0;
    let highlighted = '';
    merged.forEach(([start, end]) => {
      highlighted += this.escapeHtml(result.substring(offset, start));
      highlighted += `<mark>${this.escapeHtml(result.substring(start, end + 1))}</mark>`;
      offset = end + 1;
    });
    highlighted += this.escapeHtml(result.substring(offset));

    return highlighted;
  }

  escapeHtml(text) {
    const div = document.createElement('div');
    div.textContent = text;
    return div.innerHTML;
  }
}

// Initialize search when DOM is ready
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => new Search());
} else {
  new Search();
}
